
$color-blue: #182b89;
$color-lightblue: #3ea6df;

$transition-2s: all 0.2s ease-in-out;

// hero
.wrap-fin-hero {
    width: 100%;
    height: 100vh;
    margin: -80px auto 0;
    background-image: linear-gradient(to top, rgba(255, 255, 255, 0), rgba(24, 43, 136)), url('../../images/BG-36.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
    display: flex;
    align-items: center;
}

.fin-hero-container {
    width: 50%;
    background-color: rgba(238, 238, 238, 0.15);
    border-radius: 0 500px 500px 0;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    display: grid;
    grid-template-columns: 1fr;
    padding: 120px 0;
    align-items: center;
    justify-content: center;

    & > h1 {
        width: 70%;
        margin: 0 auto 20px;
        color: #fff;
        font-size: 36px;
        font-weight: bold;
        letter-spacing: normal;
        line-height: 1.5;
        display: grid;
        grid-template-columns: 1fr;
        & > span {
            font-size: 50px;
        }
    }

    & > p {
        width: 70%;
        margin: 0 auto;
        color: #fff;
        font-size: 24px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.29;
        letter-spacing: normal;
    }
}


// content
.wrap-fin {
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding: 90px 0 ;
}


// fin-container-1
.fin-container-1 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
}

.fin-con-1-tab-row {
    // gap: 100px;
    flex-wrap: initial;
    justify-content: space-between;

    & .flex-column {
        gap: 25px 0;
    }
}

.fin-con-1-nav-item {
    border-radius: 15px;
    -moz-box-shadow: 0 0 10px 0 rgba(62, 166, 223, 0.45) ;
    -webkit-box-shadow: 0 0 10px 0 rgba(62, 166, 223, 0.45);
    box-shadow: 0 0 10px 0 rgba(62, 166, 223, 0.45);
    text-align: center;
}

.fin-con-1-nav-link {
    color: $color-blue;
    height: 170px;
    font-size: 20px;
    font-weight: bold;
    padding: 20px;
    border-radius: 15px !important;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        color: $color-blue;
    }

    &.active {
        color: #fff;
        background-color: $color-blue !important;
    }
}




// Properties
.fin-tab-pane-2-header {
    margin: 0 0 40px;

    & > h3 {
        color: $color-blue;
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 10px;
    }
    & > p {
        color: #aeaeae;
        font-size: 16px;
        font-weight: normal;
    }
}


// 1

.fin-tab-pane-1-content {

    & > .fin-tab-pane-1-linediv {
        margin: 30px 0;
        border-bottom: solid 2px #d9d9d9;
    }

    & > .fin-tab-pane-1-input {
        margin-bottom: 30px;

        & > h4 {
            color: $color-blue;
            font-size: 16px;
            font-weight: normal;
            text-transform: capitalize;
            margin-bottom: 10px;
        }
        & input {
            width: 100%;
            color: $color-blue;
            background-color: #fff;
            padding: 10px 20px;
            font-size: 14px;
            font-weight: normal;
            border: none;
            border-radius: 10px;
            outline: 0;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
            -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
        }

        & select {
            width: 100%;
            color: $color-blue;
            background-color: #fff;
            padding: 10px 20px;
            font-size: 14px;
            font-weight: normal;
            border: none;
            border-radius: 10px;
            outline: 0;
            cursor: pointer;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;

            & option {
                padding: 11px 0;
                border: none;
                outline: 0;
                cursor: pointer;
                text-transform: capitalize;
            }
        }
    }

    & > .fin-tab-pane-calculate-btn {
        color: #fff;
        background-color: $color-lightblue;
        width: 100%;
        padding: 12px 0;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
        outline: 0;
        border: none;
        border-radius: 15px;
        transition: $transition-2s;
        &:hover {
            background-color: #64b6e3;
        }
    }


    & > .fin-tab-pane-1-type1 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 35px;
        margin-bottom: 30px;

        & > .fin-tab-pane-1-type1-input {
            padding: 10px 20px;
            display: flex;
            align-items: center;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
            border: none;
            border-radius: 10px;
            outline: 0;

            & span {
                color: $color-blue;
                background-color: #fff;
                margin-right: 20px;
                font-size: 14px;
                font-weight: bold;
                text-transform: uppercase;
            }

            & input {
                width: 100%;
                color: $color-blue;
                background-color: #fff;
                font-size: 14px;
                font-weight: normal;
                border: none;
                outline: 0;
            }
        }
    }
}


// 2
.fin-tab-pane-2-content {

    & > .fin-tab-pane-2-linediv {
        margin: 30px 0;
        border-bottom: solid 2px #d9d9d9;
    }

    & > .fin-tab-pane-2-input {
        margin-bottom: 30px;

        & > h4 {
            color: $color-blue;
            font-size: 16px;
            font-weight: normal;
            text-transform: capitalize;
            margin-bottom: 10px;
        }
        & input {
            width: 100%;
            color: $color-blue;
            background-color: #fff;
            padding: 10px 20px;
            font-size: 14px;
            font-weight: normal;
            border: none;
            border-radius: 10px;
            outline: 0;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        }

        & select {
            width: 100%;
            color: $color-blue;
            background-color: #fff;
            padding: 10px 20px;
            font-size: 14px;
            font-weight: normal;
            border: none;
            border-radius: 10px;
            outline: 0;
            cursor: pointer;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
            -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;

            & option {
                padding: 11px 0;
                border: none;
                outline: 0;
                cursor: pointer;
                text-transform: capitalize;
            }
        }

        & > .fin-tab-pane-2-saloon-type {
            margin: 12px 0 0;
            padding: 0 24px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 30px;

            & > .fin-tab-pane-2-saloon-type-1 {
                & > h5 {
                    color: $color-blue;
                    margin: 0 0 5px;
                    font-size: 16px;
                    font-weight: bold;
                }
    
                & > p {
                    color: #999;
                    font-size: 14px;
                    font-weight: normal;
                }
            }
        }

        & > .fin-tab-pane-payment {
            color: $color-blue;
            background-color: #fff;
            padding: 20px;
            border-radius: 20px;
            box-shadow: 0 0 10px 0 rgba(62, 166, 223, 0.45);
            text-align: center;
            font-size: 36px;
            font-weight: bold;
        }
    }

    & > .fin-tab-pane-calculate-btn {
        color: #fff;
        background-color: $color-lightblue;
        width: 100%;
        padding: 12px 0;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
        outline: 0;
        border: none;
        border-radius: 15px;
        transition: $transition-2s;
        &:hover {
            background-color: #64b6e3;
        }
    }
}







@media screen and (min-width: 1901px) {


}


@media screen and (max-width: 1200px) {


    .fin-container-1 {
        width: 90%;
    }


    .fin-con-1-tab-row-col {
        width: 30%;
    }

    .fin-con-1-nav-link {
        height: 130px;
        font-size: 16px;
    }
}



@media screen and (max-width: 1024px) {
    .wrap-fin-hero {
        height: 700px;
    }

    .fin-hero-container {
        width: 70%;
        padding: 90px 0;

        & > h1 {
            font-size: 32px;
            display: initial;

            & > span{
                font-size: 42px;
            }
        }
        & > p {
            font-size: 20px;
        }
    }


    

}



@media screen and (max-width: 576px) {
    
    .fin-hero-container {
        width: 80%;
        padding: 50px 0;

        & > h1 {
            font-size: 24px;

            & > span{
                font-size: 30px;
            }
        }
        & > p {
            font-size: 16px;
        }
    }



    .fin-con-1-tab-row {
        display: grid;
        grid-template-columns: 1fr;
        gap: 40px;
    }

    .fin-con-1-tab-row-col {
        width: 100%;
        overflow: scroll;
        overflow-y: hidden;

        & .flex-column {
            margin-left: -10px;
            padding: 10px 10px;
            flex-direction: initial !important;
            display: flex;
            justify-content: flex-start;
            overflow: hidden;
            gap: 0 20px;
            width: max-content;
        } 
    }

    .fin-con-1-nav-item {
        width: 200px;
        height: 100px;
    }

    .fin-con-1-nav-link {
        width: 200px;
        height: 100px;
        font-size: 14px;
    }


    // content 
    .fin-tab-pane-1-header {
        & > h3 {
            font-size: 16px;
        }
        & > p {
            font-size: 14px;
        }
    }
    .fin-tab-pane-2-header {
        & > h3 {
            font-size: 16px;
        }
        & > p {
            font-size: 14px;
        }
    }


    .fin-tab-pane-1-input {

        & select {
            -webkit-appearance: none;
        }
    }
    
    .fin-tab-pane-2-input {

        & select {
            -webkit-appearance: none;
        }
    }


}