
$color-blue: #182b89;
$bg-grey: #d8d8d8;

$transition-2s: all 0.2s ease-in-out;


nav {
    // background-color: rgba(24, 43, 136, 1);
    background: linear-gradient(0deg, rgba(62, 167, 224, 0), rgba(24, 43, 136, 1));
    width: 100%;
    height: 80px;
    max-height: 80px;
    top: 0;
    position: sticky !important;
    z-index: 100 !important;
}

.navbar-container {
    width: 95%;
    height: 80px;
    max-height: 80px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-container {
    width: 180px;

    & img {
        width: 180px;
    }
}

.nav-menu {
    width: 100%;
    height: 80px;
    margin: 0 auto;
    display: flex;
    justify-content: center;

    & > .nav-item {
        color: #fff;
        height: 65px;
        margin: 0 15px;
        display: flex;
        align-items: center;
        text-align: center;

        & > .nav-links {
            color: #fff;
            height: 65px;
            padding: 30px 30px 0;
            font-size: 14px;
            font-weight: normal;
            font-stretch: normal;
            text-decoration: none;

            &.active {
                font-weight: bold;
                background-color: rgba(255, 255, 255, 0.2);
                backdrop-filter: blur(8px);
                -webkit-backdrop-filter: blur(8px);
            }
        }
    }
}

.nav-mob-menu {
    display: none;
}


@media screen and (min-width: 1901px) {

}



@media screen and (max-width: 1200px) {

    nav {
        // background-color: rgba(24, 43, 136, 1);
        background:  rgba(24, 43, 136, 1);
    }


    .wrap-navbar {
        z-index: 100;
    }

    .logo-container img {
        width: 160px;
    }

    .nav-menu {
        display: none;
    }

    .tabl-menu {
        display: initial;
    }

    .nav-account {
        display: grid;
        grid-template-columns: 40px 40px;
        gap: 20px;
        align-items: center;
    }

    .nav-mob-menu {
        display: initial;
    }

    .mob-menu-item {
        & > .menu-icon-burger {
            color: #fff;
            font-size: 30px;
        }
    }

    /* mobile menu offcanvas  */
    .mob-menu-offcanvas {
        display: initial;
        height: fit-content;
        z-index: 0;
        margin: 80px 0 0;

        & .mob-menu-body {
            display: grid;
            grid-template-columns: 1fr;
            gap: 20px;
            padding: 20px 40px;

            & .offcanvas-link {
                color: $color-blue !important;
                text-decoration: none;
                font-size: 18px;
            }
        }

        
    }

    .offcanvas-backdrop {
        background-color: #000;
        height: 100vh;
        left: 0;
        position: fixed;
        top: 0;
        width: 100vw;
        z-index: 0;
        margin: 80px 0 0;
    }
    .offcanvas-backdrop.show, .offcanvas-backdrop.fade {
        opacity: 0.5;
        z-index: 0;
    }

}




@media screen and (max-width: 576px) {

    .logo-container img {
        width: 140px;
    }


    /* mobile menu offcanvas  */
    .mob-menu-offcanvas {
        height: fit-content;
        z-index: 0;
        margin: 80px 0 0;

        & .mob-menu-body {
            display: grid;
            grid-template-columns: 1fr;
            gap: 20px;
            padding: 20px;

            & .offcanvas-link {
                font-size: 14px;
            }
        }

        
    }
}